.posts-list--wrapper {
  border-top: $base-border;
  padding-top: 3 * $base-spacing;
}

.posts-list--post {
  padding: 0 0 3 * $base-spacing 1.5 * $base-spacing;
  border-left: $base-border;
  margin-left: 20%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;

  &--image {
    display: block;
    margin-right: 1.5 * $base-spacing;
    flex: 0 0 auto;
    width: 9 * $base-spacing;
    margin-bottom: auto;

    img {
      display: block;
      height: auto;
    }
  }

  &--tag {
    font-size: modular-scale(-2);
    margin-bottom: 1.5 * $small-spacing;
  }

  &--title {
    font-size: modular-scale(1);
  }

  &--excerpt {
    font-size: modular-scale(-1);

    a {
      display: block;
      text-transform: lowercase;
      margin-top: $small-spacing;
    }
  }
}



